body {
  margin: 0;
  padding: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(19, 59, 51)), color-stop(18%, rgb(58, 129, 121)), color-stop(41%, rgb(28, 132, 140)), color-stop(71%, rgb(2, 55, 53)));
  background: -webkit-linear-gradient(top, rgb(19, 59, 51) 0%, rgb(58, 129, 121) 18%, rgb(28, 132, 140) 41%, rgb(2, 55, 53) 71%);
  background: -o-linear-gradient(top, rgb(19, 59, 51) 0%, rgb(58, 129, 121) 18%, rgb(28, 132, 140) 41%, rgb(2, 55, 53) 71%);
  background: linear-gradient(to bottom, rgb(19, 59, 51) 0%, rgb(58, 129, 121) 18%, rgb(28, 132, 140) 41%, rgb(2, 55, 53) 71%);
  height: 100vh;
}



